:root {
    --bg-dark: #1a2f3a;
    
    --logo-height: 80px;
    --header-height: 80px;
    --asside-width: 225px;
    
    --footer-height: 40px;
    
    --shadow:
    0 2px 23px 0 rgba(0, 0, 0, 0.1),
    0 2px 49px 0 rgba(0, 0, 0, 0.06);
    
    
    --time-transition: 700ms
}

*{
    box-sizing: border-box;
    font-family: 'Montserrat,', sans-serif;
}

/* Layout em Grid */
.app{
    margin: 0;
    display: grid;
    grid-template-columns: 225px 1fr;
    grid-template-columns: var(--asside-width) 1fr;
    grid-template-rows: 80px
    1fr
    40px;
    grid-template-rows: var(--header-height)
    1fr
    var(--footer-height);
    grid-template-areas: 
        "logo header"
        "menu content"
        "menu footer";
    min-height: 100vh;
    background: #f5f5f5;
}

aside.logo{
    grid-area: logo;
}
header.header{
    grid-area: header;
}
aside.menu-area{
    grid-area: menu;
    position: relative;
    z-index: 2;
}
main.content{
    grid-area: content;
}
footer.footer{
    grid-area: footer;
}

