.fade-appear {
    opacity: 0;
}
.fade-appear.fade-appear-active {
    opacity: 1;
    transition: opacity 2000ms;
}

.fade-enter {
    visibility: hidden;
    opacity: 0;
}
.fade-enter.fade-enter-active {
    position: absolute;
    visibility: hidden;
    opacity: 0;
}
.fade-enter-done {
    position: static;
    visibility: visible;
    opacity: 1;
    transition: opacity 700ms;
    transition: opacity var(--time-transition);
    background: url(/react-crud/static/media/preload.cad0d684.gif) center no-repeat;
}

.fade-exit {
    opacity: 1;
}
.fade-exit.fade-exit-active {
    opacity: 0;
    transition: opacity 700ms;
    transition: opacity var(--time-transition);
}



main.content >div {
    box-shadow: 0px 0px 15px #0004;
    background-color: #fff;
}
header.header{
    padding: 0 15px;
    white-space: nowrap;
    overflow: hidden;
    background-color: #fff;
    box-shadow: var(--shadow);
}
header.header h1{
    font-size: 1.6em;
}

header.header p{
    font-size: 1em;
}
aside.logo{
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 3;
    background-color: var(--bg-dark);
}

aside.logo img {
    width: 100%;
    padding: 0 15px;
}



aside.menu-area{
    background-color: var(--bg-dark);
    box-shadow:  var(--shadow);
}

.menu a{
    display: block;
    padding: 15px;
    color: #fff;
    font-weight: 300;
    text-decoration: none;
}

.menu a i{
    margin-right: 5px;
}

.menu a:hover {
    background: linear-gradient(to right, var(--bg-dark), rgb(71, 71, 71));
}
footer.footer {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 0 25px;
    background: #fff;
    box-shadow: var(--shadow)
}

footer.footer span {
    font-size: .9em;
}
